.about-us {
    margin-top: 30px;
}


.welcome-about {
    margin-top: 40px;
}

.posts-container {
    padding: 50px 50px;
    width: 100%;
    max-width: 1000px;
    margin: 0 auto;

}

.post {
    position: relative;
    margin-bottom: 30px;
    padding: 15px;
    width: 100%;

    background: #494949;
    border-radius: 8px;
}


.post-header {
    margin-bottom: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.post-author {
    font-size: 20px;
    font-weight: bold;
}

.common-p {
    color: white;
}

.post-author span {
    font-size: 16px;
    font-weight: normal;
}

.post-body {
    margin-bottom: 20px;
}

.post-img {
    width: 100%;

}

.post-text {
    color: white;
    text-decoration: none;
    margin-bottom: 10px;
}

.post-img img {
    width: 100%;
    height: 100%;

    object-fit: cover;
}

.post-footer {
    display: flex;
    justify-content: flex-end;
}

.post-rating {
    margin-right: auto;
    justify-self: flex-start;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;

    background: #1c1818;
    border-radius: 50%;
}

.rate-post {
    cursor: pointer;
    margin-left: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;

    background: #1c1818;
    border-radius: 50%;
    transition: all 0.2s linear;
}

.green {
    background: #70bb55;
}
.orange {
    background: #c46d1f;
}
.red {
    background: #ea3333;
}


.rate-post:hover {
    background: #d0d0d0;
    color: #1c1818;
}

.create-post {
    width: 100%;
    margin-bottom: 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.send-btn,
.add-img-btn {
    cursor: pointer;
    width: 50px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;

    border-radius: 50%;
    background: #faefef;
}

.icon-wrapper {
    width: 30px;
    height: 30px;

    border-radius: 50%;
}

.icon-wrapper img {
    width: 100%;
    height: 100%;

}

.file-input,
.submit-input {
    position: absolute;
    visibility: hidden;
    opacity: 0;
}

.img-name {
    margin-bottom: 10px;
}

.error_container {
    width: 100%;
    position: fixed;
    left: 0;
    bottom: 0;
    font-size: 18px;
    padding: 15px 10px;
    text-align: center;

    color: #ea3636;
    background: rgba(31, 31, 31, 0.79);
}

.post-container {
    padding: 50px 50px;
    margin: 0 auto;
    width: 900px;
}

.comments-title {
    margin-bottom: 15px;
}

.comment-form {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 30px;
}

.post-header__right-side {
    display: flex;
    align-items: center;
}